.button {
  background: linear-gradient(
    89.84deg,
    #1f26c5 2.77%,
    #3b26be 26.21%,
    #6026be 45.98%,
    #8224bc 70.58%,
    #8821b9 95.38%
  );
  border: 1px solid #000000;
  border-radius: 30px;
  color: white;
  padding: 15px 30px;
  font-family: "InterRegular";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  cursor: pointer;
}
