.mainBackground {
  background-size: cover !important;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("/src/assets/images/main_background.jpg");
}

.logo {
  max-width: 88vw;
}

.messageContainer {
  background-color: rgba(0, 0, 0, 0.74);
  border: 4px solid #5019aa;
  max-height: calc(100vh - 200px);
  overflow: auto;
  padding: 20px;
  margin: 10px auto;
  max-width: 800px;
}

.message {
  color: white;
  font-size: 20px;
  text-align: left;
  font-family: CourierNewBold;
}

.buttonsContainer,
.logoContainer {
  text-align: center;
}

.ideaImage,
.paypalImage {
  position: absolute;
  top: 50px;
  cursor: pointer;
  width: 100px;
  height: 100px;
}
.paypalImage {
  right: 15px;
}

.ideaImage {
  left: 15px;
}
